import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Servant from './Servant'
import Payment from './Payment'
import AccountingStatus from './AccountingStatus'
import { formatFullData } from '@/common/common'

const entity = {
  id: {
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredEdit: false,
      requiredNew: false,
    },
    header: false,
  },
  type: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: AccountingStatus.list,
        itemText: 'name',
      },
    },
  },
  amount: {},
  payMethodId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Payment.getList,
        itemText: '_langsname',
      },
    },
    displayName: 'payMethod',
  },
  servantId: {
    displayName: 'servantName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Servant.getList,

      },
    },
  },
  createdAt: {
    formConfig: {
      default: formatFullData(0),
      disableNew: true,
      disableEdit: true,
      requiredEdit: false,
      requiredNew: false,
    },
  },
  remark: {
    formConfig: {
      requiredEdit: false,
      requiredNew: false,
    },
  },
}
const config = {
  add: function (item) {
    return hillo.post('BackendData.php?op=addAccountingLine', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('BackendData.php?op=deleteAccountingLine', { id: id })
  },

  load: async function (filter) {
    const _data = (await hillo.get('BackendData.php', Object.assign({
      op: 'getAccountingTable',
    }, ...filter)))
      .content.map((item, index) => {
        if (item.id === '') {
          item.id = 'idx' + index
        }
        return item
      })

    console.log('filter Data', _data)
    return _data
  },

}

export default IKDataEntity.ModelFactory(entity, config)
